.banner-text {
  background-color: black;
  opacity: .8;
  width: 50%;
  margin: auto;
  margin-top: 50px;
  border-radius: 10px;
  height: auto;

}

.banner-text h1 {
  font-size: 15vmin;
  font-weight: bold;
  color: white;
  margin: 10%;
  font-family: 'Raleway', sans-serif;
  display: inline-block;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text div {
  height: auto;
  overflow: auto;
}

.icon {
  -webkit-filter: invert();
          filter: invert();
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 20px;
  margin-top: 20px;
  height: 70px;
  width: 70px;
}


.btn-default {
  background-color: #ffffff;
  color: #a8780f;
  border-color: #a8780f;
  border-width: 5px;
  padding: 10px;
  font-weight: bold;
  margin-bottom: 30px;
  font-size: 16pt;
}

html,
body {
  height: 100%;
  width: 100%;
  overflow: auto;
}

body {
  background: #4B79A1;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4B79A1, #89C5D4);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+*/
}
.mdl-grid {
  align-items: 'space-between';
  height: auto;
}


@media screen and (max-width: 1200px) {

  .mdl-grid .mdl-card {
    margin-top: 50px;
    align-self: flex-start;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    height: auto;
  }

  img,
  .carousel slide {
    width: 100%;
    height: auto;
  }
}
.mdl-grid .mdl-card {
  margin-top: 50px;
  /* align-self: flex-start; */
  margin-right: auto;
  margin-left: auto;
  max-width: 600px;
  width: 100%;
  height: auto;
}

.mdl-card__supporting-text span {
  color: #000000;
  font-size: 16pt;
  font-family: 'Ubuntu', sans-serif;
  margin: 0pt;
}

.mdl-card__supporting-text {
  font-family: 'Roboto Slab', serif;
}

