.carousel.slide .carousel-control-prev-icon {
  background-color: #9e9b9b;
  color: #ffffff;
  padding: 10px;
  height: 60px;
  width: 60px;
  border-radius: 25px;
  -webkit-filter: opacity(0.75);
          filter: opacity(0.75);
  background-image: url(/MyPortfolio/static/media/arrow-back-circle-outline.1fb40817.svg);
}

.carousel.slide .carousel-control-next-icon {
  background-color: #9e9b9b;
  color: #ffffff;
  padding: 10px;
  height: 60px;
  width: 60px;
  border-radius: 25px;
  -webkit-filter: opacity(0.5);
          filter: opacity(0.5);
  background-image: url(/MyPortfolio/static/media/arrow-forward-circle-outline.10a643e7.svg);
}

.carousel-indicators li {
  background-color: #838080;
  color: #ffffff;
